<template>
  <div class="home">
    <keep-alive>
      <router-view class="route" v-if="$route.meta.keep">
        <!-- 这里是会被缓存的视图组件，比如 Home！ -->
      </router-view>
    </keep-alive>
    <router-view class="route" v-if="!$route.meta.keep">
      <!-- 这里是不被缓存的视图组件，比如 Edit！ -->
    </router-view>

    <van-tabbar
      v-show="inDebug"
      :border="false"
      v-model="active"
      safe-area-inset-bottom
      route
      :fixed="false"
    >
      <van-tabbar-item replace to="/home">
        <van-icon
          slot="icon"
          slot-scope="props"
          :name="
            props.active
              ? require('@/assets/icon/home-tabbar-icon-active.png')
              : require('@/assets/icon/home-tabbar-icon.png')
          "
        />
      </van-tabbar-item>
      <van-tabbar-item replace to="/discover">
        <van-icon
          slot="icon"
          slot-scope="props"
          :name="
            props.active
              ? require('@/assets/icon/discover-tabbar-icon-active.png')
              : require('@/assets/icon/discover-tabbar-icon.png')
          "
        />
      </van-tabbar-item>
      <van-tabbar-item replace to="/message" :info="info ? info : ''">
        <van-icon
          slot="icon"
          slot-scope="props"
          :name="
            props.active
              ? require('@/assets/icon/message-tabbar-icon-active.png')
              : require('@/assets/icon/message-tabbar-icon.png')
          "
        />
      </van-tabbar-item>
      <van-tabbar-item replace to="/personal">
        <van-icon
          slot="icon"
          slot-scope="props"
          :name="
            props.active
              ? require('@/assets/icon/personal-tabbar-icon-active.png')
              : require('@/assets/icon/personal-tabbar-icon.png')
          "
        />
      </van-tabbar-item>
    </van-tabbar>
  </div>
</template>

<script>
import Vue from "vue";
import { Tabbar, TabbarItem } from "vant";
import * as Server from "@/server";
import { mapMutations } from "vuex";

Vue.use(Tabbar).use(TabbarItem);
export default {
  name: "home",
  data() {
    return {
      active: 0,
      info: 0,
      inDebug: false
    };
  },
  methods: {
    ...mapMutations(["startDebug", "updateTrackInfo"]),
    async getConfig() {
      let data = await Server.GetVfunConfig();
      console.log("Get Config => ", data);
      if (!data || !data.data) return;
      this.inDebug = true;
      this.startDebug(true);
    },
    addVConsole() {
      let scriptNode = document.createElement("script");
      scriptNode.type = "text/javascript";
      scriptNode.src = "https://cdn.bootcdn.net/ajax/libs/vConsole/3.3.4/vconsole.min.js";
      scriptNode.setAttribute("defer", "true");
      document.body.appendChild(scriptNode);
      console.log("AddVConsole => ", scriptNode);
      scriptNode.onload = function () {
        console.log("Success add vConsole");
        let sn2 = document.createElement("script");
        sn2.type = "text/javascript";
        sn2.innerHTML = `
          var vConsole = new VConsole();
          window.xumr = new Date().toString();
          console.log('Hello world');
        `;
        sn2.setAttribute("defer", "true");
        document.body.appendChild(sn2);
      };
      scriptNode.onerror = function () {
        console.log("Failed add vConsole");
      };
    },
    getUrlparam(key, url) {
      if (!url) url = window.location.href;
      let index = url.indexOf("?");
      if (index < 0) return null;
      url = url.substr(index + 1);
      let arr = url.split("&");
      let result = null;
      arr.forEach((value) => {
        let cur = value.split("=");
        if (cur[0] === key) {
          result = cur[1];
          return;
        }
      });
      return result;
    },
    saveTrackInfo() {
      let trackInfo = JSON.parse(localStorage.getItem("trackInfo") || "{}");
      if (trackInfo.source && trackInfo.refby) {
        console.log("Already has trackInfo");
        this.updateTrackInfo(trackInfo);
        return;
      }
      let param = this.getUrlparam("param") || "";
      let arr = param.split("-");
      let source, refby;
      for(let i=0; i< arr.length; i++){
        let cur = arr[i].split("_");
        if(cur[0] === "source") source = cur[1];
        else if(cur[0] === "refby") refby = cur[1];
      }
      trackInfo = { source, refby };
      let appId = 305;
      if(source && source.length > 1) appId = 306;
      trackInfo.appId = appId;
      this.updateTrackInfo(trackInfo);
    }
  },
  beforeMount() {
    console.log("Home BeforeMount => ", new Date().toString());
    this.saveTrackInfo();
  },
  async mounted() {
    await this.getConfig();
    console.log("InDebug => ", this.inDebug);
    if (this.inDebug) this.addVConsole();
    // 接收消息
    // const { status, data } = await Server.NewMessage();
    // if (status === 0) {
    //   this.info = data || 0;
    // }
  },
};
</script>
<style lang="less">
.home {
  display: flex;
  flex-direction: column;
  height: 100%;
  overflow: hidden;

  .route {
    flex: 1;
    overflow: auto;
  }
  & /deep/ .van-tabbar {
    border-top: 1px solid rgba(70, 70, 70, 1);
  }
  & /deep/ .van-info {
    border-color: #ee0a24;
  }
}
</style>
